import LineMgr from "../data_mgr/LineMgr";
import { road, arrow } from "../helper/cavans";

const LineStyles = {
    normal: null,
    road: road,
    arrow: arrow,
};

/**
 * @classdesc
 * 
 * WebGL é»è®¤ç»å¶çº¿çæ¨¡å¼ gl.LINES åªè½ç»ä¸åç´ ççº¿ï¼æ æ³æå®çº¿çå®½åº¦ï¼è¯¥å¾å±ç¨æ¥å±ç¤ºå¯æå®å®½åº¦ççº¿å¾å±ï¼ç»§æ¿èª Layerã
 * å¦æåªéè¦ç»å¶ç®åçä¸åç´ ççº¿ï¼å¯ä»¥ä½¿ç¨ SimpleLineLayerã
 * è¯¥å¾å±å¯ä½¿ç¨é¼ æ æ¾å Pickã
 * 
 * @extends Layer
 * 
 * @param {Object} options
 * @param {String=} [options.style='normal']
 * è§£éï¼è®¾ç½®è¯¥åæ°ï¼å¯ä»¥å¨çº¿ä¸å å ä¸äºå¾å½¢æ¥éç¨äºä¸äºåºæ¯ãæ³¨æï¼è¯¥å±æ§åªå¨åå§åæ¶è¯»åä¸æ¬¡ï¼å®ä¾ååä¸å¯éè¿setOptionsæ¹æ³æ¥éç½® </br>
 * å¯éå¼ï¼ </br>
 * roadï¼å å è·¯åµç®­å¤´ï¼å¯ç¨äºéè·¯åºæ¯çå±ç¤º </br>
 * arrowï¼å å å°ç®­å¤´å¾å½¢ï¼å¯ç¨äºODåºæ¯çå±ç¤º </br>
 * @param {Object=} [options.styleOptions={}] æ§å¶è´´å¾çæ ·å¼ï¼å¯¹è±¡å·æcoloråwidthå±æ§
 * @param {String=} [options.color='rgba(25, 25, 250, 1)'] é¢è²
 * @param {String=} [options.blend='normal'] çº¿å å æ¨¡å¼ï¼å¯élighter
 * @param {String=} [options.lineJoin='miter'] çº¿çè¿æ¥æè§ï¼å¯é `miter` å°è§ã`bevel` å¹³è§ã`round` åè§
 * @param {String=} [options.lineCap='butt'] çº¿çç«¯å¤´ï¼å¯é `butt` å¹³å¤´ã`square` æ¹å¤´ã`round` åå¤´
 * @param {Number=} [options.width=4] çº¿çå®½åº¦
 * @param {Number=} [options.offset=0] æ²¿æ³çº¿æ¹åçåç§»ï¼å ä¹å¾å°ä½¿ç¨å°ï¼è®¾ç½®è¯¥å±æ§ååªè½ç¨ `butt`ç«¯å¤´å `miter`è¿æ¥ï¼ä¸ç¶ä¼åºç°é®é¢
 * @param {Boolean=} [options.antialias=false] æé¯é½¿ï¼é»è®¤å³é­ä¸º `false`
 * @param {Array.<Number>=} [options.dashArray=[0, 0]] å®ä¹èçº¿é´éçæ°ç»ï¼æ°ç»é¿åº¦ä¸º2ãæ°ç»çä¸¤ä½åå«è¡¨ç¤ºå®çº¿åèçº¿çé¿åº¦ï¼åä½åç´ ï¼å¦[10, 20]è¡¨ç¤ºå®çº¿10pxï¼èçº¿20px
 * @param {Number=} [options.dashOffset=0] èçº¿åç§»éï¼åä½åç´ ï¼å¯ä»¥éè¿å®æ¶æ¹åè¯¥å¼æ¥å®ç°å¨ç»
 * @param {Boolean=} [options.animation=false] è®¾ç½®è¯¥åæ°æ¥å®ç°èèªçº¿å¨ç»ï¼ä¸é¢çå±æ§çæä¾èµè¯¥å¼ä¸º trueãæ³¨æï¼è¯¥å±æ§åªå¨åå§åæ¶è¯»åä¸æ¬¡ï¼å®ä¾ååä¸å¯éè¿setOptionsæ¹æ³æ¥éç½®
 * @param {Number=} [options.interval=0.1] è¯¥åæ°æå®æ¯æ¡çº¿æ®µçé¿åº¦ï¼å¼ä¸ºç²å­é¿åº¦å æ°æ®ä¸­æé¿ççº¿æ´ä½é¿åº¦çæ¯ä¾
 * @param {Number=} [options.duration=2] å¨ç»çå¾ªç¯æ¶é´ï¼åä½ä¸ºç§
 * @param {Number=} [options.trailLength=0.5] æå°¾é¿åº¦å é´éçæ¯ä¾
 * @param {Number=} [options.minZoom=4] å°å¾è§éå¤§äºç­äºä¸å®çº§å«æ¶å¼å¯å¨ç»
 * @param {Number=} [options.maxZoom=21] å°å¾è§éå°äºç­äºä¸å®çº§å«æ¶å¼å¯å¨ç»
 */
class LineLayer extends Layer {
    constructor(options) {
        super(options);
    }

    getDefaultOptions() {
        return {
            style: "normal",
            styleOptions: {},
            color: "rgba(25, 25, 250, 1)",
            blend: "normal",
            lineJoin: "miter",
            lineCap: "butt",
            width: 4,
            offset: 0,
            antialias: false,
            dashArray: [0, 0],
            dashOffset: 0,
            animation: false,
            interval: 0.1,
            duration: 2,
            trailLength: 0.5,
            minZoom: 4,
            maxZoom: 21,
        };
    }

    initialize(gl) {
        const options = this.getOptions(),
            defines = [];

        // pick
        if (options.enablePicked) {
            defines.push("PICK");
        }

        // texture
        if (LineStyles[options.style]) {
            this.isUseTexture = true;
            defines.push("USE_TEXTURE");

            const texture = LineStyles[options.style](options.styleOptions);
            this.setOptions({ texture: texture });
            this.loadTexture();
        }

        // animate
        if (options.animation === true) {
            this.isAnimateLine = true;
            this.date = new Date();
            this.autoUpdate = true;
            defines.push("USE_LINE_ANIMATION");
        }

        this.program = new Program(
            gl,
            {
                shaderId: "line",
                defines: defines,
            },
            this
        );

        this.vao = new VertexArrayObject();
    }

    onChanged(options, data) {
        const self = this;
        if (this.gl) {
            let dashArray = options.dashArray;
            dashArray =
                !!this.isUseTexture || !!dashArray[1] || !!this.isAnimateLine;

            const dataMgr = new LineMgr({
                    dash: dashArray,
                    cap: options.lineCap,
                    join: options.lineJoin,
                    miterLimit: options.miterLimit,
                    thickness: options.width,
                }),
                pickColors = [];

            for (let i = 0; i < data.length; i++) {
                let coords = data[i].geometry.coordinates;
                if (coords && 0 < coords.length) {
                    if (
                        "Polygon" !== data[i].geometry.type &&
                        "MultiLineString" !== data[i].geometry.type
                    ) {
                        coords = [coords];
                    }
                    coords = coords.map((a) =>
                        a.map((point) => this.normizedPoint(point))
                    );
                }

                // èµ·ç¹ä½ç½®
                const preStartIndex = dataMgr.complex.startIndex;

                const color = this.normizedColor(
                    this.getValue("color", data[i])
                );
                coords = this.addMultipleCoords(coords);
                for (let j = 0; j < coords.length; j++) {
                    coords[j].forEach(function (line) {
                        dataMgr.extrude(line, color);
                    });
                }

                // pick
                if (options.enablePicked) {
                    const pColor = self.indexToRgb(i);
                    for (
                        let j = preStartIndex;
                        j < dataMgr.complex.startIndex;
                        j++
                    ) {
                        pickColors.push(
                            pColor[0] / 255,
                            pColor[1] / 255,
                            pColor[2] / 255
                        );
                        if (options.repeat) {
                            pickColors.push(
                                pColor[0] / 255,
                                pColor[1] / 255,
                                pColor[2] / 255
                            );
                            pickColors.push(
                                pColor[0] / 255,
                                pColor[1] / 255,
                                pColor[2] / 255
                            );
                        }
                    }
                }
            }

            const complexData = dataMgr.complex;
            if (dashArray) {
                for (let p = 0; p < complexData.positions.length / 6; p++) {
                    complexData.positions[6 * p + 5] = complexData.maxDistance;
                }
            }

            this.updateBuffer(complexData, pickColors);
        }
    }

    updateBuffer(complexData, pickColors) {
        const gl = this.gl;

        const { positions, normals, colors, uvs, indices } = complexData;

        this.vertexBuffers = [
            // positions
            new VertexBuffer({
                gl: gl,
                data: positions,
                attributes: [
                    {
                        name: "a_position",
                        size: 3,
                    },
                    {
                        name: "a_distance",
                        size: 1,
                    },
                    {
                        name: "a_width",
                        size: 1,
                    },
                    {
                        name: "a_total_distance",
                        size: 1,
                    },
                ],
            }),
            // normals
            new VertexBuffer({
                gl: gl,
                data: normals,
                attributes: [
                    {
                        name: "a_normal",
                        size: 3,
                    },
                ],
            }),
            // colors
            new VertexBuffer({
                gl: gl,
                data: colors,
                attributes: [
                    {
                        name: "a_color",
                        size: 4,
                    },
                ],
            }),
            // pick
            ...this.getCommonBuffers({ pickData: pickColors }),
        ];

        // uva
        if (this.isUseTexture) {
            this.vertexBuffers.push(
                new VertexBuffer({
                    gl: gl,
                    data: uvs,
                    attributes: [
                        {
                            name: "uv",
                            size: 2,
                        },
                    ],
                })
            );
        }

        this.indexBuffer = new IndexBuffer({
            gl: gl,
            data: indices,
        });
    }

    render(transferOptions) {
        const gl = transferOptions.gl,
            matrix = transferOptions.matrix;

        if (this.indexBuffer.numberOfIndices === 0) return;

        const options = this.getOptions(),
            program = this.program;

        program.use(gl);

        let uniforms = Object.assign(this.getCommonUniforms(transferOptions), {
            u_matrix: matrix,
            u_zoom_units: this.map.getZoomUnits(),
            u_dash_array: options.dashArray,
            u_dash_offset: options.dashOffset,
            u_antialias: options.antialias,
            u_offset: options.offset,
        });

        // çº¹çæ¨¡å¼
        if (this.isUseTexture) {
            uniforms = Object.assign(uniforms, {
                u_texture_width: options.width,
                u_texture_margin: 140,
                u_sampler: this.texture,
            });
        }

        // å¨ç»æ¨¡å¼
        if (this.isAnimateLine) {
            const zoom = this.map.getZoom();
            uniforms = Object.assign(uniforms, {
                u_time: (new Date() - this.date) / 1e3,
                u_animate:
                    zoom >= options.minZoom &&
                    zoom <= options.maxZoom &&
                    this.autoUpdate
                        ? true
                        : false,
                u_duration: options.duration,
                u_interval: options.interval,
                u_trail_length: options.trailLength,
            });
        }
        program.setUniforms(uniforms);

        // æ··åæ¨¡å¼
        gl.enable(gl.BLEND);
        gl.blendEquation(gl.FUNC_ADD);
        if (options.blend && "lighter" === options.blend) {
            gl.blendFunc(gl.SRC_ALPHA, gl.ONE);
        } else {
            gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA);
        }

        // ç»å®é¡¶ç¹å¹¶ç»å¶
        this.vao.bind({
            gl,
            program,
            vertexBuffers: this.vertexBuffers,
            indexBuffer: this.indexBuffer,
        });

        gl.drawElements(
            gl.TRIANGLES,
            this.indexBuffer.numberOfIndices,
            this.indexBuffer.indexDatatype,
            0
        );
    }

    loadTexture(callback) {
        const options = this.getOptions();
        options.texture
            ? this.gl.textureManager.load(options.texture, (texture) => {
                  this.texture = texture;
                  callback && callback();
                  this.webglLayer.render();
              })
            : ((this.texture = null), callback && callback());
    }
}

export default LineLayer;
