import Layer from "./Layer";

import { IndexBuffer, VertexBuffer } from "../core/Buffer";
import VertexArrayObject from "../core/VertexArrayObject";
import Program from "../core/Program";

import ShapeMgr from "../data_mgr/ShapeMgr";

// æ¸²æç±»å
const LayerStyles = {
    window: 1,
    windowAnimation: 2,
    gradual: 3,
    water: 6,
};

/**
 * @classdesc
 * 
 * ç¨æ¥å±ç¤ºå¤§æ°æ®çç«ä½å¤è¾¹å½¢ï¼ç»§æ¿èª Layer
 * å¯ä½¿ç¨é¼ æ æ¾å Pick
 * 
 * @extends Layer
 * 
 * @param {Object} options
 * @param {Number | Function=} [options.baseHeight=0] åºåé«åº¦
 * @param {Number | Function=} [options.height=0] é«åº¦
 * @param {String | Function=} [options.color='rgba(25, 25, 250, 1)'] é¢è²
 * @param {String} options.topColor é¡¶é¨é¢è²
 * @param {Number=} [options.opacity=1] æ´ä½éæåº¦
 * @param {String | HTMLImageElement=} options.texture é»è®¤è´´å¾
 * @param {String | HTMLImageElement=} options.topTexture é¡¶é¨è´´å¾
 * @param {Boolean=} [options.isTextureFull=false] æ¯å¦ä¸ºæ´é¢è´´å¾
 * @param {Number=} [options.textureScale=1] è´´å¾ç¼©æ¾æ¯ä¾ï¼åªå¯¹éæ´é¢è´´å¾æ¨¡å¼ææ
 * @param {String=} options.blend é¢è²å å æ¨¡å¼ï¼å¯élighter
 * @param {Number=} [options.riseTime=0] æ¥¼ååå§ååèµ·å¨ç»çæ¶é´ï¼åä½æ¯«ç§
 * @param {String=} [options.style='normal']
 * è§£éï¼ä¸äºç¹æ </br>
 * å¯éå¼ï¼ </br>
 * normalï¼é»è®¤ï¼æ­£å¸¸ </br>
 * windowï¼çªæ·ææ </br>
 * windowAnimationï¼çªæ·å¨ç»ææ </br>
 * gradualï¼æ¸åææ </br>
 * @param {Boolean=} [options.useLight=true] æ¯å¦ä½¿ç¨åç§ææ
 * @param {Array.<Number>=} [options.lightDir=[0, -1, 2]] ä¾§é¢åç§æ¹ä½åé
 */
class ShapeLayer extends Layer {
    constructor(options) {
        super(options);

        // å¤æ­æ¯å¦éè¦èªå¨æ´æ°
        if (
            "windowAnimation" === this.options.style ||
            0 < this.options.riseTime
        ) {
            this.autoUpdate = true;
        }
        this.textureLoaded = false;
    }

    getDefaultOptions() {
        return {
            color: "rgba(50, 50, 230, 1.0)",
            opacity: 1.0,
            isTextureFull: false,
            textureScale: 1,
            useLight: true,
            riseTime: 0,
        };
    }

    initialize(gl) {
        this.dataMgr = new ShapeMgr(this, gl);
        this.isUseTexture = false;

        // program
        this.program = new Program(
            gl,
            {
                shaderId: "shape",
                defines: this.options.enablePicked ? ["PICK"] : [],
            },
            this
        );
        // vao
        this.vao = new VertexArrayObject();
        // init Time
        this.initializeTime = new Date();
    }

    updateBuffer(dataMgr) {
        const gl = this.gl;

        const {
            vertex,
            color,
            height,
            texture,
            index,
            pickColorVertex,
        } = dataMgr.outBuilding3d;

        this.vertexBuffers = [
            // point
            new VertexBuffer({
                gl: gl,
                data: vertex,
                attributes: [
                    {
                        name: "a_pos",
                        size: 4,
                    },
                    {
                        name: "a_normal",
                        size: 3,
                    },
                ],
            }),
            // color
            new VertexBuffer({
                gl: gl,
                data: color,
                attributes: [
                    {
                        name: "a_color",
                        size: 4,
                    },
                    {
                        name: "a_pre_color",
                        size: 4,
                    },
                ],
            }),
            // height
            new VertexBuffer({
                gl: gl,
                data: height,
                attributes: [
                    {
                        name: "a_base_height",
                        size: 1,
                    },
                    {
                        name: "a_height",
                        size: 1,
                    },
                    {
                        name: "a_pre_height",
                        size: 1,
                    },
                ],
            }),
            // texure
            new VertexBuffer({
                gl: gl,
                data: texture,
                attributes: [
                    {
                        name: "a_texture_coord",
                        size: 2,
                    },
                ],
            }),
            // pick
            ...this.getCommonBuffers({ pickData: pickColorVertex }),
        ];

        this.indexBuffer = new IndexBuffer({
            gl: gl,
            data: index,
        });
    }

    onChanged(options, dataArray) {
        if (this.gl) {
            this.loadTextureTime && clearTimeout(this.loadTextureTime);

            this.textureLoaded = false;
            this.loadTextureTime = setTimeout(() => {
                this.loadTexture(() => {
                    this.textureLoaded = true;

                    this.dataMgr.parseData(dataArray);
                    this.updateBuffer(this.dataMgr);

                    this.dataTime = new Date();
                    this.webglLayer.render();
                });
            }, 0);
        }
    }

    render(transferOptions) {
        const gl = transferOptions.gl,
            matrix = transferOptions.matrix;

        if (this.indexBuffer && this.indexBuffer.numberOfIndices > 0) {
            const options = this.getOptions();

            const program = this.program;
            program.use(gl);

            if (!this.isUseTexture || this.textureLoaded) {
                // æ¸²æç±»å
                const style = LayerStyles[options.style] || 0;

                // åç§ç¸å³åæ°
                let light_dir = [0, -1, 2];
                if (options.lightDir) {
                    light_dir = [
                        options.lightDir[0] || 0,
                        options.lightDir[1] || 0,
                        options.lightDir[2] || 0,
                    ];
                }

                // ç¦ç¨èé¢åé¤
                gl.disable(gl.CULL_FACE);

                // æ··åæ¨¡å¼
                if (options.blend) {
                    gl.enable(gl.BLEND);
                    gl.blendFunc(gl.ONE, gl.ONE);
                    gl.blendEquation(gl.FUNC_ADD);
                }

                // å¦æé«åº¦ä¸º 0
                if (0 === options.height) {
                    gl.enable(gl.BLEND);
                    gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA);
                }

                // å¦ææ¯æ¸åå¢ä½æ¨¡å¼
                if (3 === style) {
                    gl.depthMask(false);
                    gl.enable(gl.BLEND);
                    gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA);
                } else {
                    gl.depthMask(true);
                }

                // è®¾ç½®uniforms
                program.setUniforms(
                    Object.assign(this.getCommonUniforms(transferOptions), {
                        u_matrix: matrix,
                        u_zoom_unit: this.normizedHeight(
                            1,
                            this.map.getCenter()
                        ),
                        // defines
                        defines: {
                            useLight: options.useLight,
                            useTexture: this.isUseTexture,
                            useTopTexture: !!options.topTexture,
                            useTopColor: !!options.topColor,
                        },

                        // æ¸²ææ¨¡å¼åéç¨æ¸²æé¢è²
                        u_style: style,
                        u_alpha: parseFloat(options.opacity),

                        // çº¹çç¸å³
                        u_sampler: this.gl.textureManager.get(options.texture),
                        u_top_sampler: this.gl.textureManager.get(
                            options.topTexture
                        ),
                        u_top_color: this.normizedColor(options.topColor),

                        // åç§ç¸å³
                        u_side_light_dir: light_dir,

                        // æ¶é´ç¸å³
                        u_time: new Date() - this.initializeTime,
                        u_dataTime: new Date() - this.dataTime,
                        u_riseTime: options.riseTime,
                    })
                );

                this.vao.bind({
                    gl,
                    program,
                    vertexBuffers: this.vertexBuffers,
                    indexBuffer: this.indexBuffer,
                });

                gl.drawElements(
                    gl.TRIANGLES,
                    this.indexBuffer.numberOfIndices,
                    this.indexBuffer.indexDatatype,
                    0
                );
            }
        }
    }

    loadTexture(callBack) {
        const options = this.getOptions();

        if (options.texture || options.topTexture) {
            this.isUseTexture = true;

            this.gl.textureManager.loadAndAdd(
                () => {
                    callBack && callBack();
                },
                options.texture,
                options.topTexture
            );
        } else {
            this.isUseTexture = false;
            callBack && callBack();
        }
    }
}

export default ShapeLayer;
